:root {
  --body-font: "Inter", sans-serif;
  --theme-bg: #1f1d2b;
  --body-color: #808191;
  --button-bg: #353340;
  --border-color: rgba(128, 129, 145, .24);
  --video-bg: #252936;
  --delay: 0s;
}

body {
  font-family: var(--body-font);
  color: var(--body-color);
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.section {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

.container {
  max-width: 1280px;
  width: 100%;
  height: 100vh;
  background-color: var(--theme-bg);
  margin-left: auto;
  margin-right: auto;
  font-size: 15px;
  font-weight: 500;
  display: flex;
  position: relative;
  overflow: hidden;
}

.logo {
  width: 30px;
  height: 30px;
  color: #fff;
  background-color: #22b07d;
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  display: none;
}

.logo-expand {
  color: #fff;
  font-size: 19px;
  font-weight: 600;
  line-height: 34px;
  text-decoration: none;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.logo-expand:before {
  content: "";
  background: var(--theme-bg);
  width: 200px;
  height: 70px;
  z-index: -1;
  position: absolute;
  top: -30px;
  left: 0;
}

.sidebar {
  width: 220px;
  height: 100%;
  flex-direction: column;
  flex-shrink: 0;
  padding: 30px;
  transition-duration: .2s;
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
}

.side-menu__item:hover .sidebar-link, .side-menu__item.is-active .sidebar-link {
  color: #fff;
  font-weight: 600;
}

.side-menu__item:hover:nth-child(2n+1) svg, .side-menu__item.is-active:nth-child(2n+1) svg {
  background: #ff7551;
}

.side-menu__item:hover:nth-child(2n) svg, .side-menu__item.is-active:nth-child(2n) svg {
  background: #32a7e2;
}

.side-menu__item:hover:nth-child(2n+3) svg, .side-menu__item.is-active:nth-child(2n+3) svg {
  background: #6c5ecf;
}

.sidebar.collapse {
  width: 90px;
  border-right: 1px solid var(--border-color);
}

.sidebar.collapse .logo-expand, .sidebar.collapse .side-title {
  display: none;
}

.sidebar.collapse .logo {
  display: flex;
}

.sidebar.collapse .side-wrapper {
  width: 30px;
}

.sidebar.collapse .side-menu svg {
  margin-right: 30px;
}

.side-menu {
  flex-direction: column;
  display: flex;
}

.side-menu a {
  color: var(--body-color);
  align-items: center;
  margin-top: 26px;
  text-decoration: none;
  display: flex;
}

.side-menu svg {
  width: 30px;
  background-color: var(--button-bg);
  border-radius: 10px;
  flex-shrink: 0;
  margin-right: 16px;
  padding: 8px;
}

.side-menu svg:hover {
  color: #fff;
}

.side-title {
  letter-spacing: .07em;
  color: #fff;
  margin-bottom: 24px;
  font-size: 12px;
}

.side-wrapper {
  border-bottom: 1px solid var(--border-color);
  width: 145px;
  padding: 36px 0;
}

.side-wrapper + .side-wrapper {
  border-bottom: none;
}

@keyframes bottom {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

.anim {
  animation: bottom .8s var(--delay) both;
}

.main-container {
  flex-direction: column;
  flex-grow: 1;
  padding: 0 30px 30px;
  display: flex;
  overflow: auto;
}

.header {
  flex-shrink: 0;
  align-items: center;
  padding: 30px;
  display: flex;
}

.search-bar {
  height: 34px;
  width: 100%;
  max-width: 450px;
  display: flex;
}

.search-bar input {
