  background-position: 96%;
  background-repeat: no-repeat;
  background-size: 14px;
  border: none;
  border-radius: 8px;
  padding: 0 40px 0 16px;
  font-size: 14px;
  font-weight: 500;
  box-shadow: 0 0 0 2px rgba(134, 140, 160, .02);
}

.user-settings {
  flex-shrink: 0;
  align-items: center;
  margin-left: auto;
  padding-left: 20px;
  display: flex;
}

.user-settings svg {
  width: 10px;
  flex-shrink: 0;
}

.user-img {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
  flex-shrink: 0;
}

.user-name {
  color: #fff;
  margin: 0 6px 0 12px;
  font-size: 14px;
}

@media screen and (max-width: 767px) {
  .user-settings svg, .user-name {
    display: none;
  }
}

.main-header {
  color: #fff;
  z-index: 11;
  background: linear-gradient(#1f1d2b 0%, #1f1d2b 78%, rgba(31, 29, 43, 0) 100%);
  padding-bottom: 20px;
  font-size: 30px;
  font-weight: 700;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.most-watched {
  color: #fff;
  margin: 30px 0 20px;
  font-size: 24px;
  font-weight: 500;
}

.main-films {
