  display: flex;
}

.main-film__rating {
  align-items: center;
  padding-bottom: 10px;
  display: flex;
}

.main-film__rating .tips {
  flex-direction: column-reverse;
  align-items: flex-start;
}

.main-film__title {
  max-width: 12ch;
  letter-spacing: 1px;
  color: #fff;
  margin-bottom: 30px;
  font-size: 25px;
  font-weight: 600;
}

.main-film {
  background-color: #31abbd;
  background-image: url("https://assets.codepen.io/3364143/skate-removebg-preview.png");
  background-position-x: 150px;
  background-repeat: no-repeat;
  background-size: 80%;
  border-radius: 20px;
  flex-direction: column;
  align-self: stretch;
  padding: 30px;
  transition: background .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.main-film + .main-film {
  background-image: url("https://c0.anyrgb.com/images/1020/945/venice-beach-2018-outdoors-sport-men-jumping-desert-sunset-extreme-sports-one-person-action.jpg");
  background-color: unset;
  filter: saturate(1.4);
  background-position-x: 0;
  background-size: 139%;
  margin-left: 20px;
}

@media screen and (min-width: 768px) {
  .main-film, .main-film + .main-film {
    width: 50%;
    padding: 30px;
  }

  .main-film {
    background-blend-mode: overlay;
    background-position-x: center;
    background-size: cover;
  }
}

@media screen and (min-width: 1280px) {
  .main-film {
    width: 65%;
  }

  .main-film + .main-film {
    width: 35%;
  }
}

.rating__wrapper svg {
  color: #e7bb7d;
  border-color: #ffe6b2;
}

.film-detail {
  margin-left: 0;
}

.main-film__selection {
  color: #fff;
  background: rgba(21, 13, 13, .44);
  border-radius: 6px;
  padding: 3px 8px;
  font-size: 12px;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.rating-value {
  width: 52px;
  height: 52px;
  color: #fff;
  border: 2px solid #0daabc;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-top: 14px;
  padding: 4px;
  font-weight: 600;
  display: flex;
}

.rating__wrapper {
  flex-shrink: 0;
  position: relative;
}

.rating__wrapper svg {
  width: 16px;
  color: #0daabc;
  background-color: #fff;
  border: 2px solid #0daabc;
  border-radius: 50%;
  padding: 2px;
  position: absolute;
  bottom: 5px;
  right: 0;
}

.film-name {
  color: #fff;
  margin-bottom: 8px;
  font-size: 15px;
  font-weight: 500;
}

.film-info {
  color: #fff;
  font-size: 13px;
  font-weight: 400;
}

.film-detail {
  margin-left: 16px;
}

.seperate {
  width: 3px;
  height: 3px;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 50%;
  margin: 0 6px;
  display: inline-block;
}

.seperate.video-seperate {
  background-color: var(--body-color);
}

@media screen and (max-width: 767px) {
  .main-films {
    flex-wrap: wrap;
  }

  .main-film, .main-film + .main-film {
    width: 100%;
  }

  .videos {
    grid-template-columns: 1fr;
  }

  .main-film + .main-film {
    background-size: cover;
    margin-top: 20px;
    margin-left: 0;
  }
}

@media screen and (max-width: 475px) {
  .main-film__title {
    font-size: 20px;
  }

  .film-name {
    font-size: 14px;
  }

  .main-film__rating {
    flex-direction: column-reverse;
    align-items: flex-start;
  }

  .film-detail {
    margin-left: 0;
  }

  .main-film .rating-value {
    margin-top: 14px;
  }

  .main-container {
    padding: 0 20px 20px;
  }

  .header {
    padding: 20px;
  }

  .sidebar.collapse {
    width: 40px;
  }

  .sidebar {
    align-items: center;
  }

  .container {
    height: 100vh;
    max-height: 100%;
  }
}

::-webkit-scrollbar {
  width: 6px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(21, 20, 26, .63);
  border-radius: 10px;
}

.videos {
  width: 100%;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  grid-template-columns: repeat(1, 1fr);
  display: grid;
}

.video {
  background-color: var(--video-bg);
  border-radius: 20px;
  transition: transform 5s ease-in;
  position: relative;
  overflow: hidden;
}

.video-wrapper {
  height: 200px;
  position: relative;
}

.video-name {
  color: #fff;
  background-color: var(--video-bg);
  z-index: 9;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  padding: 12px 20px 0;
  font-size: 16px;
  line-height: 1.4em;
  display: -webkit-box;
  position: relative;
  overflow: hidden;
}

.video-view {
  background-color: var(--video-bg);
  padding: 12px 20px 20px;
  font-size: 12px;
  position: relative;
}

.video-selection {
  color: rgba(255, 255, 255, .85);
  z-index: 1;
  background: rgba(21, 13, 13, .44);
  border-radius: 6px;
  padding: 3px 8px;
  font-size: 12px;
  position: absolute;
  top: 10px;
  right: 8px;
}

.video-poster {
  transition: transform .7s ease-in-out;
}

.video:hover {
  transform-origin: center;
  transform: scale(1.4);
}

.video:hover .video-selection {
  display: none;
}

.video:hover .video-rating {
  z-index: 10;
  bottom: -65px;
  right: -3px;
  transform: scale(.6);
}

.video-rating {
  z-index: 99;
  transition: all .4s;
  position: absolute;
  bottom: -25px;
  right: 10px;
}

.video-rating svg {
  color: #fff;
  border-color: var(--video-bg);
  background-color: #0aa0f7;
}

@media screen and (min-width: 768px) {
  .videos {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1280px) {
  .videos {
    grid-template-columns: repeat(4, 1fr);
  }
}

